.message {
    display: grid;
    background: linear-gradient(to top, #48007a, #020005);
    grid-template-columns: 150px 1fr 300px;
}

.image_logo {
    margin: 10px;
    align-self: end;
}

.image_logo img {
    border-radius: 50%;
    width: 100%;
}

.message_block {
    margin: 20px;
    padding: 15px;
    border: maroon 5px solid;
    border-radius: 25px;
    background-color: seashell;
}

.message_block .name {
    color: cornflowerblue;
    font-size: x-large;
    font-weight: bold;
}

.message_block .mini_message {

}

.message_block .time {
    text-align: end;
}
